<script>
    export default {
        name: "SeNavigation",
        data() {
            return {
                navLinksShown: false,
            }
        },
        computed: {
            navLinksContainerClass() {
                if (this.navLinksShown) {
                    return ''
                }
                return ['hidden'];
            }
        },
        methods: {
            toggleNavLinks() {
                if (this.hideNavLinksTimeout) {
                    clearInterval(this.hideNavLinksTimeout);
                    this.hideNavLinksInterval = undefined;
                }
                this.navLinksShown = !this.navLinksShown;
            },
            hideNavLinks() {
                this.hideNavLinksTimeout = setTimeout(() =>{
                    this.navLinksShown = false;
                }, 250);
            }
        }
    }
</script>

