<template>
  <div class="">
    <article class="prose lg:prose-xl mx-auto">
      <h2>Your Scripts</h2>
      <p class="">
        <input type="button"
               v-on:click="goToNewDocument()"
               value="New Script"
               class="px-4 h-9 rounded-full bg-green-500 hover:bg-green-400 text-white outline-none cursor-pointer"/>
      </p>
      <se-document-browser v-bind:rows="rows" v-if="ready"/>
    </article>
  </div>
</template>

<script>
    import SeDocumentBrowser
        from "@/components/se-document-browser/SeDocumentBrowser";
    import LocalDocumentAsyncStorage from "@/storage/LocalDocumentAsyncStorage";
    import {newId} from "@/storage/SeStorageUtils";

    const documentStorage = new LocalDocumentAsyncStorage();

    export default {
        name: "EditorDocumentList",
        components: {SeDocumentBrowser},
        data() {
            return {
                ready: false,
                documents: {},
                documentStorage,
            }
        },
        created() {
            this.documentStorage.find().then((documents) => {
                this.documents = documents;
                this.ready = true;
            }).catch((err) => {
                console.error(`error: ${err}`);
            })
        },
        methods: {
            goToNewDocument() {
                this.$router.history.push({
                    name: 'editor-document',
                    params: {
                        id: newId(),
                    }
                })
            }
        },
        computed: {
            rows() {
                return Object.keys(this.documents).map((id) => {
                    let doc = this.documents[id];
                    return {
                        key: id,
                        text: doc.title,
                        onOpen: () => {
                            this.$router.history.push({ name: 'editor-document', params: {id: id} })
                        }
                    }
                })
            }
        }
    }
</script>

