const outcomeCells = [
    {
        name: 'counts',
        placeholder: '0',
    },
    {
        name: 'moveName',
        placeholder: 'Move Name',
    },
    {
        name: 'outcome',
        placeholder: 'Move outcome',
    },
    {
        name: 'cues',
        placeholder: 'Related cue',
    },
];

const iconSofter = '<svg xmlns="http://www.w3.org/2000/svg" class="icon icon-tabler icon-tabler-volume-3" width="24" height="24" viewBox="0 0 24 24" stroke-width="1.5" stroke="#2c3e50" fill="none" stroke-linecap="round" stroke-linejoin="round">\n' +
    '  <path stroke="none" fill="none" d="M0 0h24v24H0z"/>\n' +
    '  <path stroke="currentColor" fill="none" d="M6 15 h-2a1 1 0 0 1 -1 -1v-4a1 1 0 0 1 1 -1h2l3.5 -4.5a.8 .8 0 0 1 1.5 .5v14a.8 .8 0 0 1 -1.5 .5l-3.5 -4.5" />\n' +
    '  <path stroke="currentColor" fill="none" d="M16 10l4 4m0 -4l-4 4" />\n' +
    '</svg>';
const iconLouder = '<svg xmlns="http://www.w3.org/2000/svg" class="icon icon-tabler icon-tabler-volume" width="24" height="24" viewBox="0 0 24 24" stroke-width="1.5" stroke="#2c3e50" fill="none" stroke-linecap="round" stroke-linejoin="round">\n' +
    '  <path stroke="none" fill="none" d="M0 0h24v24H0z"/>\n' +
    '  <path stroke="currentColor" fill="none" d="M15 8a5 5 0 0 1 0 8" />\n' +
    '  <path stroke="currentColor" fill="none" d="M17.7 5a9 9 0 0 1 0 14" />\n' +
    '  <path stroke="currentColor" fill="none" d="M6 15 h-2a1 1 0 0 1 -1 -1v-4a1 1 0 0 1 1 -1h2l3.5 -4.5a.8 .8 0 0 1 1.5 .5v14a.8 .8 0 0 1 -1.5 .5l-3.5 -4.5" />\n' +
    '</svg>';

export default class OutcomesBlock {
    static get toolbox() {
        return {
            title: "Outcomes Block",
            icon: '<svg xmlns="http://www.w3.org/2000/svg" class="icon icon-tabler icon-tabler-run" width="22" height="22" viewBox="0 0 24 24" stroke-width="2" stroke="#000000" fill="#ffffff" stroke-linecap="round" stroke-linejoin="round">\n' +
                '  <path stroke="none" fill="none" d="M0 0h24v24H0z"/>\n' +
                '  <circle stroke="currentColor" cx="13" cy="4" r="1" />\n' +
                '  <path fill="none" stroke="currentColor" d="M4 17l5 1l.75 -1.5" />\n' +
                '  <path fill="none" stroke="currentColor" d="M15 21l0 -4l-4 -3l1 -6" />\n' +
                '  <path fill="none" stroke="currentColor" d="M7 12l0 -3l5 -1l3 3l3 1" />\n' +
                '</svg>',
        }
    }

    constructor({data}) {
        this.data = data;
    }

    render() {
        this.wrapper = document.createElement('div');
        this.wrapper.classList.add('outcomes-block', 'rounded-md', 'selected:bg-blue-100');
        this.wrapper.innerHTML = '';

        let cell = document.createElement('div');
        cell.classList.add('outcomes-block-icons');
        cell.id = 'icons';
        cell.innerHTML = '';
        this.wrapper.appendChild(cell);

        outcomeCells.forEach((cellProperties) => {
            const cellName = cellProperties.name;
            let cell = document.createElement('div');
            cell.classList.add('outcomes-block-cell');
            cell.setAttribute('contenteditable', 'true');
            cell.setAttribute('data-placeholder', cellProperties.placeholder);
            cell.id = cellName;
            if (this.data) {
                if (this.data[cellName]) {
                    cell.innerHTML = this.data[cellName];
                }
            }
            this.wrapper.appendChild(cell);
        });

        this._refreshIcons()

        return this.wrapper;
    }

    _toggleWithSofter() {
        this._toggleVolume('softer');
        this._refreshIcons();
    }

    _toggleWithLouder() {
        this._toggleVolume('louder');
        this._refreshIcons();
    }

    _toggleVolume(volume) {
        if (volume === this.data.volume) {
            this.data.volume = '';
            return;
        }
        this.data.volume = volume;
    }

    _refreshIcons() {
        const iconsElement = this.wrapper.querySelector('#icons');
        if (this.data.volume === 'softer') {
            iconsElement.innerHTML = iconSofter;
        } else if (this.data.volume === 'louder') {
            iconsElement.innerHTML = iconLouder;
        } else {
            iconsElement.innerHTML = '';
        }

    }

    renderSettings() {
        const settings = [
            {
                name: 'withSofter',
                icon: iconSofter,
                onClick: () => this._toggleWithSofter(),
            },
            {
                name: 'withLouder',
                icon: iconLouder,
                onClick: () => this._toggleWithLouder(),
            }
        ];
        const wrapper = document.createElement('div');

        settings.forEach( tune => {
            let button = document.createElement('div');

            button.classList.add('cdx-settings-button');
            button.innerHTML = tune.icon;

            button.addEventListener('click', tune.onClick);

            wrapper.appendChild(button);
        });

        return wrapper
    }

    save(wrapper) {
        return {
            counts: wrapper.querySelector('#counts').innerHTML,
            moveName: wrapper.querySelector('#moveName').innerHTML,
            outcome: wrapper.querySelector('#outcome').innerHTML,
            cues: wrapper.querySelector('#cues').innerHTML,
            volume: this.data.volume,
        }
    }
}
