<template>
  <div>
    <se-editor v-if="ready" v-bind:key="id" v-bind:document="document" v-on:onSave="handleSave"/>
  </div>
</template>

<script>
import SeEditor from "@/components/se-editor/SeEditor";
import LocalDocumentAsyncStorage from "@/storage/LocalDocumentAsyncStorage";
import {DocumentAsyncStorageError} from "@/storage/DocumentAsyncStorage";

const documentStorage = new LocalDocumentAsyncStorage();

export default {
    name: 'Editor',
    components: {SeEditor},
    props: ['id'],
    data() {
        return {
            document: null,
            ready: false,
            documentStorage: documentStorage,
        }
    },
    created() {
    },
    mounted() {
        this.openDocument(this.id);
    },
    methods: {
        handleSave(doc) {
            if (this.documentStorage) {
                this.documentStorage.put(this.id, doc).then(() => {
                    this.document = Object.assign({}, doc);
                    console.log(`saved document ${doc.title} (${this.id})`)
                }).catch((err) => {
                    console.error(`error: ${err}`);
                })
            }
        },
        openDocument(id) {
            console.log('opening', id);
            this.ready = false;
            this.documentStorage.get(id).then(docGot => {
                this.document = docGot;
                this.id = id;
                this.ready = true;
            }).catch((err) => {
                if (err === DocumentAsyncStorageError.DOCUMENT_NOT_FOUND) {
                    console.log('document not found, but continuing anyway');
                    this.id = id;
                    this.ready = true;
                    return
                }
                console.error(`error: ${err}`);
            })
        }
    },
    watch: {
        id(id) {
            this.openDocument(id);
        },
    }
}
</script>
