import {DocumentAsyncStorageError} from "./DocumentAsyncStorage";

export default class LocalDocumentAsyncStorage {
    constructor() {
        this.provider = '9440E756-1B93-445A-936E-9562FFA7EBB8';
        this.storage = window.localStorage;
    }
    getLocalData() {
        let data = this.storage.getItem(this.provider);

        if (data) {
            return JSON.parse(data)
        }

        return {}
    }
    putLocalData(data) {
        this.storage.setItem(this.provider, JSON.stringify(data));
    }
    get(id) {
        return new Promise((resolve, reject) => {
            if (!id) {
                reject(DocumentAsyncStorageError.BAD_ID_ARG);
            }
            const data = this.getLocalData();
            if (data[id]) {
                resolve(Object.assign({}, data[id]))
            }
            reject(DocumentAsyncStorageError.DOCUMENT_NOT_FOUND)
        })
    }
    put(id, doc) {
        return new Promise((resolve, reject) => {
            if (!id) {
                reject(DocumentAsyncStorageError.BAD_ID_ARG);
            }
            if (!doc) {
                reject(DocumentAsyncStorageError.BAD_DOC_ARG);
            }
            const data = this.getLocalData();
            data[id] = Object.assign({}, doc);
            this.putLocalData(data);
            resolve();
        })
    }
    find() {
        return new Promise((resolve) => {
            const data = this.getLocalData();
            return resolve(data);
        })
    }
    delete(id) {
        return new Promise((resolve, reject) => {
            if (!id) {
                reject(DocumentAsyncStorageError.BAD_ID_ARG);
            }
            const data = this.getLocalData();
            delete data[id];
            this.putLocalData(data);
            resolve();
        })
    }
}
