<template>
  <div class="w-full bg-white border-indigo-500 border-2 rounded overflow-hidden">
    <se-document-browser-row class="border-b border-indigo-100 hover:bg-indigo-100"
                             v-bind:class="rowStyle(row)"
                             v-for="(row) in rows"
                             v-bind:key="row.key"
                             v-bind:text="row.text"
                             v-on:onClick="() => {handleClick(row)}">
    <template  v-if="isSelected(row)">
      <input type="button"
             value="Open"
             class="rounded-full px-4 bg-indigo-500 text-white text-sm cursor-pointer outline-none hover:bg-indigo-400 capitalize h-9 my-auto"
             v-on:click="() => {handleOpen(row)}"
      />
    </template>
    </se-document-browser-row>
  </div>
</template>

<script>
    import SeDocumentBrowserRow
        from "@/components/se-document-browser/SeDocumentBrowserRow";
    export default {
        name: "SeDocumentBrowser",
        components: {SeDocumentBrowserRow},
        props: ['rows'],
        data() {
            return {
                selectedKey: null,
            }
        },
        methods: {
            handleClick(row) {
              this.selectedKey = row.key
            },
            handleOpen(row) {
                if (row.onOpen) {
                    row.onOpen(row);
                }
            },
            isSelected(row) {
                return row.key === this.selectedKey
            },
            rowStyle(row) {
                return this.isSelected(row) ? [
                    'bg-indigo-200',
                    'border-indigo-200',
                    'hover:bg-indigo-200',
                ] : ''
            }
        },
    }
</script>

