<template>
  <div class="max-w-full w-full ">
    <div id="editor-ribbon">
      <div id="editor-document-title" class="bg-white p-2 text-indigo-700">
        <input class="w-full truncate p-2" type="text" v-model="title" v-on:blur="handleSave"/>
      </div>
    </div>
    <div id="editor-container" class="wide mx-auto max-w-4xl font-sans text-md pt-8 prose max-w-4xl"></div>
  </div>
</template>

<script>
    import EditorJS from '@editorjs/editorjs';
    import OutcomesBlock
        from '@/components/se-editor/tools/outcomes-block/OutcomesBlock';
    import '@/components/se-editor/tools/outcomes-block/OutcomesBlock.css';

    const Header = require('@editorjs/header');

    export default {
        name: 'se-editor',
        props: ['document'],
        data() {
            return {
                title: 'Untitled',
            }
        },
        methods: {
            createEditor() {
                this.editor = new EditorJS({
                    holder: 'editor-container',
                    tools: {
                        header: {
                            class: Header,
                        },
                        outcomes: {
                            class: OutcomesBlock,
                            inlineToolbar: ['bold', 'italic'],
                        },
                    },
                    data: this.document ? this.document : {},
                    onChange: () => {
                        this.$emit('onChange', {editor: this.editor});
                        this.handleSave();
                    },
                });
                console.log('created editor');
            },
            destroyEditor() {
                if (this.editor) {
                    this.editor.isReady.then(() => {
                        this.editor.destroy();
                        console.log('destroyed editor');
                    })
                }
            },
            handleSaveMetadata(data) {
                data.title = this.title;
                return data;
            },
            handleSave() {
                if (this.editor) {
                    this.editor.save().then(data => {
                        this.$emit('onSave', this.handleSaveMetadata(data));
                    })
                }
            },
            updateEditorMetadata() {
                this.title = this.document ? this.document.title : this.title;
            }
        },
        mounted() {
            this.createEditor();
            this.updateEditorMetadata();
        },
        destroyed() {
            this.destroyEditor()
        },
    }
</script>

<style>
  #editor-container {
  }
  #editor-container.wide {
    width: 100%;
    max-width: 100%;
  }

  #editor-container.wide .ce-block__content {
    width: calc(100% - 42px);
    max-width: 100%;
  }

  #editor-container.wide .ce-toolbar__content {
    width: calc(100% - 42px);
    max-width: 100%;
  }

  @media screen and (min-width: 650px) {
    #editor-container.wide .ce-block__content, #editor-container.wide .ce-toolbar__content {
      width: calc(100% - 128px);
      max-width: 100%;
    }
  }

</style>
